<template>
	<Tabs value="psw" class="tab">
		<Tab-pane label="修改密码" name="psw" class="tab-item">
			<Form ref="form" :model="formItem" :label-width="80" :rules="ruleValidate" class="from-self">
				<Form-item label="旧密码" prop="oldPassword">
					<Input type="password" :maxlength="20" v-model="formItem.oldPassword" placeholder="请输入旧密码"
						   style="width: 400px"></Input>
				</Form-item>
				<Form-item label="新密码" prop="newPassword">
					<Input type="password" :maxlength="20" v-model="formItem.newPassword" placeholder="请输入新密码（不少于6位）"
						   style="width: 400px"></Input>
				</Form-item>
				<Form-item>
					<i-button type="primary" size="large" @click="submit('form')" :loading="formItem.loading">
						<span v-if="!formItem.loading">确定</span>
						<span v-else>修改中</span>
					</i-button>
					<i-button type="ghost" size="large" style="margin-left: 8px" @click="$router.go(-1)">取消</i-button>
				</Form-item>
			</Form>
		</Tab-pane>
		<i-button type="text" slot="extra" class="extra-btn" icon="ios-undo-outline" @click="$router.go(-1)">返回</i-button>
	</Tabs>
</template>
<script>
	let validatePassword = (rule, value, callback) => {
		let regex = /^[a-z0-9_-]{6,20}$/;
		if (!regex.exec(value)) {
			callback(new Error('密码应由6至20位字母或数字组成'));
		} else {
			callback();
		}
	};
	export default {
		data () {
			return {
				formItem: {
					oldPassword: '',
					newPassword: '',
					loading: false
				},
				ruleValidate: {
					oldPassword: [
						{required: true, message: '旧密码不能为空', trigger: 'blur'},
						{required: true, trigger: 'blur', validator: validatePassword}
					],
					newPassword: [
						{required: true, message: '新密码不能为空', trigger: 'blur'},
						{required: true, trigger: 'blur', validator: validatePassword}
					]
				}
			}
		},
		created: function () {

		},
		methods: {
			submit: function (name) {
				let _this = this;
				this.$refs[name].validate((valid) => {
					if (valid) {
						let userInfo = JSON.parse(sessionStorage.getItem('userInfo'));
						let timer = setTimeout(function () {
							_this.$Message.loading({
								content: '请求提交中...',
								duration: 0
							});
						}, 500);
						_this.formItem.loading = true;
						_this.$http.post('/user/resetpwd', {
							userName: userInfo.accountName,
							password: _this.formItem.oldPassword,
							repassword: _this.formItem.newPassword
						})
							.then(function (response) {
								var res = response.data;
								clearTimeout(timer);
								_this.$Message.destroy();
								if (res.code == 1) {//修改成功
									_this.$Message.success({
										content: `${res.msg},请重新登录！`
									})
									_this.loginOut();
								} else {
									_this.formItem.loading = false;
									_this.$Message.error({
										content: res.msg,
										duration: 3
									})
								}
							})
							.catch(function (error) {
								clearTimeout(timer);
								_this.$Message.destroy();
								_this.formItem.loading = false;
								_this.$Message.error({
									content: error,
									duration: 3
								})
							})

					} else {
						_this.$Message.error('表单验证失败!');
					}
				})

			},
			loginOut: function () {
				sessionStorage.removeItem('userInfo');
				this.$router.replace('/login');
			},
		}
	}
</script>
<style>
	.tab {
		padding-top: 15px;
	}

	.ivu-tabs-tab {
		font-size: 16px;
	}

	.layout-content .extra-btn {
		font-size: 14px;
		color: #20abff;
	}

	.from-self {
		padding-top: 50px;
	}

	.block-item {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
	}
</style>



